import { Component } from '@angular/core';
import { NavController, Events, ModalController,AlertController,Platform } from 'ionic-angular';
import { Storage } from '@ionic/storage';

import { EmailComposer } from '@ionic-native/email-composer';
import { InAppBrowser } from '@ionic-native/in-app-browser';

import {PremiumModal} from '../../modals/premium/premium';

@Component({
  selector: 'page-settings',
  templateUrl: 'settings.html'
})
export class SettingsPage {
    
    public languages: Array<any>;
    public settings: any;
    public premium:boolean;
    public restrictions:any;

    constructor(public navCtrl: NavController, private storage: Storage, public events: Events, private emailComposer: EmailComposer, private iab: InAppBrowser, public modalCtrl: ModalController, private alertCtrl: AlertController, public plt: Platform) {
        this.languages = [];
        this.premium = false;
        
        this.settings = {language:"EN", translations:[], historyMax:10};

        this.storage.get("languages").then((data) => {
            if (data){
                this.languages = data;
            }
        })

        this.storage.get("settings").then((data) => {
            if (data){
                this.settings = data;
            }
        });
        
        this.storage.get("premium").then((data) => {
            if (data){
                this.premium = data;
            }
        });        
        
        events.subscribe('premium:purchased', () => {
            this.premium = true;
        }); 
        
        this.restrictions = {dictationsLimit:100, dictationsTotal:0,translationsLimit:5};    
        
        this.storage.get("restrictions").then((data) => {
            if (data){
                this.restrictions = data;
            }
        });         
        
                         
    }
    
    
    public updateSettings(){

        if (!this.premium && this.settings.translations.length > this.restrictions.translationsLimit){
            
            while(this.settings.translations.length > this.restrictions.translationsLimit){
                this.settings.translations.pop();
            }
            
            //show alert that you can only pick 5
             let data = {
                 title: "Error",
                 subTitle: "Free limit exceeded",
                 message: "Upgrade to premium to have more than " + this.restrictions.translationsLimit + " simultaneous live translations.",
                 buttons: [
                     {
                         text: 'Upgrade',
                         handler: data => {
                             this.upgrade();
                         }
                     },
                     {
                         text: 'Dismiss',
                         role: 'cancel'
                     }                                    
                 ]
             }

             let alert = this.alertCtrl.create(data);
             alert.present();                            
        }        
        
        
        this.storage.set("settings",this.settings);
        this.events.publish('settings:updated', this.settings);
    }
    
    public upgrade(){
        let modal = this.modalCtrl.create(PremiumModal);
        modal.onDidDismiss(data => {
            if (!data){return;}
            this.storage.set('premium', true);
            this.events.publish('premium:purchased');   
        })        
        modal.present();        
    }
    
    public openRate(){
        if (this.plt.is("ios")){
            window.open("https://itunes.apple.com/WebObjects/MZStore.woa/wa/viewSoftware?id=1345427430", '_system');
        }
        else{
            window.open("https://play.google.com/store/apps/details?id=com.globenotes.app", '_system');
        }
    }
    public openFollow(){
        window.open("https://www.facebook.com/globenotesapp", '_system');
    }
    
    public openEmail(){
        let email = {
          to: ["constellation1130@gmail.com"],
          subject: 'Subject for your support query',
          body: 'Provide details about the support you require.',
          isHtml: true
        };        

        this.emailComposer.open(email);
    }

}



