import { Component } from '@angular/core';
import { NavController, ModalController, Platform, NavParams, ViewController, AlertController } from 'ionic-angular';
import { Storage } from '@ionic/storage';
import { InAppPurchase } from '@ionic-native/in-app-purchase';


@Component({
    selector: 'premium',
    templateUrl: 'premium.html'
})

export class PremiumModal {

    public product: any;
    
    constructor(public platform: Platform, public params: NavParams, public viewCtrl: ViewController, public storage: Storage, private iap: InAppPurchase, public alertCtrl: AlertController) {
        this.product = {price:9.99, title: "Premium", productId:"com.globenotes.app.premium"};
        
    this.iap
        .getProducts(['com.globenotes.app.premium'])
        .then((products) => {
            if (products.length > 0){
                this.product = products[0];
            }
        })
        .catch((err) => {
            console.log(err);
        });        
        
        
    }
    
    
    public buyPremium(){

        this.iap
            .buy('com.globenotes.app.premium')
            .then((data)=> {
                this.viewCtrl.dismiss(true);
            })
            .catch((err)=> {
                alert(JSON.stringify(err));
                console.log(err);
            });        
    }
    
    

    public restorePremium(){
        this.iap.restorePurchases().then((products)=>{
            for (let product of products){
                if (product.productId === this.product.productId){
                    this.viewCtrl.dismiss(true);
                    return;
                }
            }
            
            
            const alert = this.alertCtrl.create({
                title: 'Error',
                subTitle: "Could not restore premium as we couldn't find it in your purchases.",
                buttons: ['OK']
            });
            alert.present();              
            
            
        })
        .catch(()=>{
            const alert = this.alertCtrl.create({
                title: 'Error',
                subTitle: "There was an error trying to restore your purchase.",
                buttons: ['OK']
            });
            alert.present();            
        })
    }

  
  
  
  
    dismiss() {
        this.viewCtrl.dismiss(false);
    }
}



